body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

input[type="checkbox"],
input[type="radio"] {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 0;
}

body {
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-weight: 400;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    color: rgba(0, 0, 0, .80);
}

.container {
    width: 100%;
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
}

@media screen and (min-width: 1200px) {
    .container {
        padding-left: 30px;
        padding-right: 30px;
    }
}

/**
  Link
**/

a {
    color: #8cbeb7;
    text-decoration: none;
    word-wrap: break-word;
    cursor: pointer;
}

a:hover {
    color: #a9ddd6;
    text-decoration: none;
}

/**
  Button
**/


.btn {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    background-image: none;
    border: 1px solid transparent;
    white-space: nowrap;
    padding: 0 20px;
    font-size: 14px;
    border-radius: 4px;
    height: 45px;
    line-height: 45px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-transition: all .3s cubic-bezier(.645, .045, .355, 1);
    transition: all .3s cubic-bezier(.645, .045, .355, 1);
    position: relative;
    color: rgba(0, 0, 0, .65);
    background-color: transparent;
    border-color: #094139;
    outline: none;
}

.btn-block {
    display: block;
    width: 100%;
}

.btn-lg {
    padding: 0 30px;
}

.btn-primary {
    box-shadow: 0 0 10px mediumspringgreen;
    color: #fff;
    background-color: #35605a;
    border-color: #2e3532;
}

.btn-primary:hover, .btn-primary:focus, .btn-primary:active, .btn-primary.active {
    box-shadow: 0 0 25px mediumspringgreen;
    /*background-color: #6f9f98;*/
    border-color: #6f9f98;
}

.btn-success {
    color: #fff;
    background-color: #52c41a;
    border-color: #52c41a;
}

.btn-success:hover, .btn-success:focus, .btn-success:active, .btn-success.active {
    background-color: #52c41a;
    border-color: #52c41a;
}


.btn-link {
    border: none;
    height: 34px;
    padding: 0 15px;
}

.btn-link:hover {
    background-color: rgba(158, 158, 158, 0.20);
}


/**
  Form
**/

.form-item {
    margin-bottom: 18px;
}

.form-item .btn {
    cursor: pointer;
}

.form-item label {
    font-size: 0.85em;
    font-weight: 500;
    display: inline-block;
    margin-bottom: 5px;
    color: rgba(0, 0, 0, .65);
}

.form-control {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    display: inline-block;
    padding: 4px 11px;
    width: 100%;
    height: 45px;
    font-size: 0.87em;
    line-height: 45px;
    color: rgba(0, 0, 0, .80);
    background-color: #fff;
    background-image: none;
    border: 1px solid #e8e8e8;
    border-radius: 4px;
    transition: all .3s;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
}


.form-control:hover, .form-control:focus, .form-control:active {
    border-color: #a9ddd6;
    outline: 0;
    border-right-width: 1px !important;
}

.form-control:focus, .form-control:active {
    box-shadow: 0 0 0 2px rgba(24, 144, 255, .2);
}

.form-control.invalid {
    border-color: #f5222d;
}

.form-control.invalid:focus, .form-control.invalid:active {
    box-shadow: 0 0 0 2px rgba(245, 34, 45, .2);
}

.form-control[disabled], fieldset[disabled] .form-control {
    cursor: not-allowed;
}

.form-control[disabled], .form-control[readonly], fieldset[disabled] .form-control {
    background-color: #f6f8fa;
    opacity: 1;
}

.form-label {
    margin-bottom: 10px;
}

/**
  Or separator
**/

.or-separator {
    border-bottom: 1px solid #6f9f98;
    padding: 10px 0;
    position: relative;
    display: block;
    margin-top: 20px;
    margin-bottom: 30px;
    font-size: 1em;
}

.or-text {
    position: absolute;
    left: 46%;
    top: 0;
    background: #35605a;
    padding: 10px;
    color: rgba(0, 0, 0, .45);
}

body {
    background-color: #3c5757;
}
